
import Card from 'vux/src/components/card/index.vue'


export default {
name:'car',
props:['id'],
components:{
Card
},
data:function(){
return{
car:'',
carall:[],
car2018:[],
car2019:[]
}
},
methods:{
toprice:function(carid){
this.$router.push('/price/' + carid);
}
},
beforeRouteEnter:function(to, from ,next){
next(function(vm){
vm.$http.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id).then(function(response){
vm.car = response.data.data;
vm.carall = response.data.data.list;


for(var i = 0;i < response.data.data.list.length;i++){


if(response.data.data.list[i].market_attribute.year == "2019"){
vm.car2019.push(response.data.data.list[i]);
}else if(response.data.data.list[i].market_attribute.year == "2018"){
vm.car2018.push(response.data.data.list[i]);
}
}
});
});
},
beforeRouteUpdate:function(to, from, next){
var app = this;
this.$http.get("/v2-car-getInfoAndListById.html?SerialID=" + to.params.id).then(function(response){
app.car = response.data.data;
app.carall = response.data.data.list;
for(var i = 0;i < response.data.data.list.length;i++){
if(response.data.data.list[i].market_attribute.year == "2019"){
app.car2019.push(response.data.data.list[i]);
}else if(response.data.data.list[i].market_attribute.year == "2018"){
app.car2018.push(response.data.data.list[i]);
}
}
});
next();
}
}



